.catch-section-mobile {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    background-image: url('./image/background.jpeg'); /* Add your background image */
    background-size: cover; /* Make sure the background covers the entire section */
    background-position: center; /* Center the background */
}

.catch-me-container-mobile {
    position: absolute;
    cursor: pointer;
    transition: top 0.5s cubic-bezier(0.4, 0, 0.2, 1), left 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    will-change: top, left;
}

.catch-box {
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative; 
    transition: transform 0.3s ease;
}

.catch-image {
    width: 130px; /* Adjust size as needed */
    height: auto; 
}

.hover-text {
    position: absolute;
    bottom: -30px;
    color: #111111;
    font-size: 1.5rem;
    font-weight: bold;
}

.mb-modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
    animation: fadeIn 0.5s ease-in-out;
}

.mb-modal-content {
    background-color: #0047ab; /* Superman blue */
    color: white;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    width: 80%; /* Responsive width */
    max-width: 400px; /* Limit max width */
    border: 5px solid #e31937; /* Superman red */
    animation: slideUp 0.5s ease-in-out;
}

/* Keyframe Animations */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes slideUp {
    from {
        transform: translateY(100%);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

/* Optional fade-out animation when closing the modal */
.mb-modal.fade-out {
    animation: fadeOut 0.5s ease-in-out forwards;
}

.profile-pic {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin: 10px auto;
    display: block;
}

.close {
    cursor: pointer;
    font-size: 1.5rem;
    color: white;

}

.contact-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
}

.icon {
    font-size: 2rem;
    color: #e31937; /* WhatsApp color */
}


.catch-image.fly {
    animation: flyEffect 1s ease-in-out infinite; /* Activate fly animation */
}

@keyframes flyEffect {
    0% {
        transform: scale(1) rotate(0deg);
    }
    50% {
        transform: scale(1.1) rotate(10deg);
    }
    100% {
        transform: scale(1) rotate(0deg);
    }
}

.catch-contact-button {
    background-color: #007bff; /* Bootstrap primary color */
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    margin: 5px 0;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
}

.catch-contact-button:hover {
    background-color: #0056b3; /* Darker shade for hover effect */
}
.catch-icon {
    margin-right: 10px; /* Space between icon and text */
    font-size: 20px; /* Adjust icon size */
}

.f-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8); /* Semi-transparent black background */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    transition: opacity 0.3s ease; /* Fade-in transition */
}

.f-modal-content {
    background-color: #f4f4f9; /* Soft modern background */
    padding: 40px;
    border-radius: 15px;
    max-width: 450px;
    width: 90%; /* Responsive width */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    text-align: center;
    transition: transform 0.3s ease; /* Smooth animation */
    transform: scale(1.05);
}

.f-modal-content h2 {
    margin-bottom: 20px;
    color: #333333;
    font-size: 1.8rem;
    font-weight: bold;
}

.f-modal-content p {
    color: #777777;
    font-size: 1rem;
    margin-bottom: 10px;
}

.f-modal-content textarea {
    width: 100%;
    padding: 15px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 8px;
    margin-bottom: 20px;
    resize: none;
    outline: none;
    transition: border 0.2s ease-in-out;
}

.f-modal-content textarea:focus {
    border-color: #2962FF;
}

.f-modal-submit-button {
    background-color: #2962FF;
    color: white;
    padding: 12px 25px;
    font-size: 1rem;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.f-modal-submit-button:hover {
    background-color: #00337a;
}

.f-modal-close-button {
    background-color: #ff0000;
    color: white;
    padding: 10px 20px;
    font-size: 1rem;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    margin-top: 15px;
    transition: background-color 0.3s ease;
}

.f-modal-close-button:hover {
    background-color: #cc0000;
}

.feedback-anonymous-text {
    color: #555555;
    font-size: 0.9rem;
    margin-bottom: 15px;
    font-style: italic;
}

.feedback-submitted-message {
    color: #2962FF;
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 20px;
}

/* Add subtle entry animation */
.f-modal-overlay {
    opacity: 0;
    animation: fadeIn 0.3s forwards;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.f-modal-content {
    transform: scale(0.95);
    animation: scaleUp 0.3s forwards;
}

@keyframes scaleUp {
    0% {
        transform: scale(0.95);
    }
    100% {
        transform: scale(1);
    }
}

  